@import "../../styles/base/variables.scss";
@import "../../styles/base/responsiveMixin.scss";

.worksMenu {
  position: fixed;
  z-index: 5;
  color: $mainDark;
  right: 10px;
  left: auto;
  @include atMedium {
    left: 10px;
    right: auto;
  }
  height: calc((100vh - 64px - 50px) / 4);

  transition: transform 0.3s ease-in;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &:hover {
    transform: rotate(90deg);
  }
}

.worksPage {
  margin: 0 auto;
  margin-bottom: 120px;
  width: 100%;
  height: calc(100vh - 64px - 50px);
  overflow: hidden;

  // background: red;

  // padding: 2.2rem;
  // padding-top: 50px;
  // padding-bottom: 50px;

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  justify-content: space-between;

  @include atSmall {
    align-items: center;
  }
  // justify-content: space-around;

  // display: grid;
  // grid-gap: 20px;
  // grid-template-areas:
  //   "title title title"
  //   " switch content content";
}

.itemFade-enter {
  opacity: 0;
  transform: scale3d(1, 0, 1);
}
.itemFade-enter-active {
  opacity: 1;
  transform: scale3d(1, 1, 1);
  transition: all 500ms ease-in;
}
.itemFade-exit {
  opacity: 1;
  transform: scale3d(1, 1, 1);
}
.itemFade-exit-active {
  opacity: 0;
  transform: scale3d(1, 0, 1);
  transition: all 500ms ease-in;
}
